<template>
    <el-dialog class="edit-auth" title="新增证书" :visible="isShow" width="40%" @close="closeDialog">
        <el-form class="u-edit-from" :modle="ruleFrom" :rules="rules" ref="ruleFrom" label-width="160px" label-position="left">
            <el-form-item label="认证类型：" prop="authType">
                <el-select v-model="ruleFrom.authtype" placeholder="请选择认证类型">
                    <el-option label="实名认证" value="1"></el-option>
                    <el-option label="基础认证" value="2"></el-option>
                    <el-option label="职业认证" value="3"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="名称：" prop="name">
                <el-input v-model="ruleFrom.name" placeholder="请输入名称"></el-input>
            </el-form-item>
            <el-form-item label="证书名称：" prop="authName">
                <el-input v-model="ruleFrom.authName" placeholder="请输入证书名称"></el-input>
            </el-form-item>
            <el-form-item label="技能标签：" prop="skillLabel">
                <el-select v-model="ruleFrom.skillLabel" placeholder="请选择技能标签">
                    <el-option label="技能1" value="1"></el-option>
                    <el-option label="技能2" value="2"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="App所填写字段：" prop="appWord">
                <el-checkbox-group v-model="checkedCities">
                    <el-checkbox v-for="city in cities" :label="city" :key="city">{{city}}</el-checkbox>
                </el-checkbox-group>
            </el-form-item>
            <el-form-item label="审核路径：" prop="authPath">
                <el-radio v-model="radio" label="1">人工审核</el-radio>
                <el-radio v-model="radio" label="2">系统自动审核</el-radio>
            </el-form-item>
            <el-form-item label="App上传图片数量：" prop="uploadNumber">
                <el-select v-model="ruleFrom.uploadNum" placeholder="请选择图片数量">
                    <el-option
                        v-for="(v,k) in uploadList"
                        :key="k"
                        :label="v"
                        :value="v"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="上传样图：" prop="uploadDemo">
                <div class="sample-list">

                    <div class="upload-sample" v-for="(item, index) in getUploadNum" :key="index">
                        <el-upload
                            class="avatar-uploader"
                            action="https://jsonplaceholder.typicode.com/posts/"
                            :show-file-list="false"
                            :on-success="handleImage(index)">
                            <img v-if="srcList[index]" :src="srcList[index]" class="avatar">
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                        <el-input v-model="input1" placeholder="请输入内容" style="width: 140px;"></el-input>
                    </div>
                </div>
            </el-form-item>
        </el-form>
        <div slot="footer" >
            <el-button @click="closeDialog">确定</el-button>
            <el-button type="primary" @click="closeDialog">取消</el-button>
        </div>
    </el-dialog>
</template>
<script>
    export default {
        props: {
            isShow: Boolean,
        },
        data() {
            return {
                ruleFrom: {
                    name: '',
                    authtype: '',
                    authName: '',
                },
                rules: {
                    name: [
                        { required: true, message: '请输入活动名称', trigger: 'blur' },
                        { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
                    ],
                    authType: [
                        { required: true, message: '请选择认证类型', trigger: 'change' }
                    ],
                    skillLabel: [
                        { required: true, message: '请选择技能标签', trigger: 'change' }
                    ],
                    appWord: [
                        {required: true, message: '请选择技能标签', trigger: 'change' }
                    ],
                    authPath: [
                        {required: true, message: '请选择审核路径', trigger: 'blur' }
                    ],
                    uploadNumber: [
                        {required: true, message: '请选择上传图片数量', trigger: 'blur' }
                    ],
                    uploadDemo: [
                        {required: true, message: '请上传样图', trigger: 'blur' }
                    ]

                },
                checkedCities: ['上海', '北京'],
                cities: ['上海', '北京', '广州', '深圳'],
                radio: '1',
                imageUrl: '',
                input1: '',
                uploadList: {
                    one: '1',
                    two: '2',
                    three: '3',
                    four: '4',
                    five: '5'
                },
                srcList: []
            }
        },
        computed: {
            getUploadNum() {
                return Number(this.ruleFrom.uploadNum) || 1
            }
        },
        methods: {
            closeDialog() {
                this.$emit('closeDiloag', false)
            },
            handleImage(index) {

            }
        }
    }
</script>
<style lang="scss">
    .edit-auth {
        .sample-list {
            display: flex;
            align-items: center;
            flex-wrap: wrap;
            margin-left: -20px;

            .upload-sample {
                width: 140px;
                margin-left: 20px;
                margin-bottom: 20px;

                .avatar-uploader .el-upload {
                    border: 1px dashed #d9d9d9;
                    border-radius: 6px;
                    cursor: pointer;
                    position: relative;
                    overflow: hidden;
                }

                .avatar-uploader .el-upload:hover {
                    border-color: #409EFF;
                }

                .avatar-uploader-icon {
                    font-size: 28px;
                    color: #8c939d;
                    width: 140px;
                    height: 140px;
                    line-height: 140px;
                    text-align: center;
                }

                .avatar {
                    width: 140px;
                    height: 140px;
                    display: block;
                }
            }
        }
    }
</style>