<style lang="scss">
    @import "./css/style.scss";
</style>
<template>
    <div id="authenList">
        <ToolBar>
            <div>
                <el-button type="primary" icon="el-icon-plus" size="small" @click="editAuth(false, null)">新增认证</el-button>
            </div>
            <div>
                <el-select v-model="searchParams.postType" size="small" clearable placeholder="认证类型"
                           style="width: 140px">
                    <el-option
                        v-for="(v,k) in $Cfg.postType"
                        :key="k"
                        :label="v"
                        :value="k">
                    </el-option>
                </el-select>

                <el-select v-model="searchParams.postStatus" size="small" clearable placeholder="证书名称"
                           style="width: 120px">
                    <el-option
                        v-for="(v,k) in $Cfg.postStatus"
                        :key="k"
                        :label="v"
                        :value="k">
                    </el-option>
                </el-select>
                <el-select v-model="searchParams.postStatus" size="small" clearable placeholder="状态"
                           style="width: 120px">
                    <el-option
                        v-for="(v,k) in $Cfg.postStatus"
                        :key="k"
                        :label="v"
                        :value="k">
                    </el-option>
                </el-select>
                <el-button type="success" size="small" @click="">查询</el-button>
                <el-button type="warning" size="small" @click="">重置</el-button>
            </div>
        </ToolBar>
        <el-table
            :data="tableData"
            border
            style="width: 100%">
            <el-table-column
                prop="date"
                label="ID"
                width="180">
            </el-table-column>
            <el-table-column
                prop="name"
                label="认证类型"
                width="180">
            </el-table-column>
            <el-table-column
                prop="address"
                label="证书名称"
                width="180">
            </el-table-column>
            <el-table-column
                prop="address"
                label="技能标签">
            </el-table-column>
            <el-table-column
                prop="address"
                label="创建日期">
            </el-table-column>
            <el-table-column
                label="操作"
                width="144">
                <template slot-scope="scope">
                    <el-button v-if="scope.row.isOpen" @click="enable(scope.row)" type="success" size="small"
                    >启用
                    </el-button>
                    <el-button v-else @click="forbidden(scope.row)" type="danger" size="small"
                    >禁用
                    </el-button>
                    <el-button @click="editAuth(true, scope.row)" type="primary" size="small"
                    >修改
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <Pagination
            :params="searchParams"
            :requestFunc="requestFunc"
            @returnData="returnData"
        />
        <Edit :isShow="showDialog" @closeDiloag="operateEdit" />
    </div>
</template>

<script>
    import ToolBar from '@/components/ToolBar.vue';
    import HelpHint from '@/components/HelpHint.vue';
    import Pagination from '@/components/Pagination.vue';
    import paginationMixin from './mixin/page.js'
    import Edit from './Edit'

    export default {
        mixins: [paginationMixin],
        data() {
            return {
                showDialog: false,
                tableData: [{
                    date: '2016-05-03',
                    isOpen: true,
                    name: 'Lorem ipsum dolor sit amet,',
                    address: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Asperiores fugit in quae vero. Adipisci blanditiis dignissimos eum facere laudantium quasi ratione repellat vitae! Alias consequatur dolores enim neque similique unde.'
                }, {
                    date: '2016-05-02',
                    isOpen: false,
                    name: 'Lorem ipsum dolor sit amet,',
                    address: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Asperiores fugit in quae vero. Adipisci blanditiis dignissimos eum facere laudantium quasi ratione repellat vitae! Alias consequatur dolores enim neque similique unde.'
                }]
            }
        },
        methods: {
            editAuth(isEdit, row) {
                console.log(isEdit)
                this.showDialog = true
            },
            operateEdit(val) {
                this.showDialog = false
            },
            forbidden(row) {
                this.$confirm('确定禁用该认证?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$message({
                        type: 'success',
                        message: '禁用认证成功!'
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消禁用认证'
                    });
                });
            },
            enable(row) {
                this.$confirm('确定启用该认证?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'success'
                }).then(() => {
                    this.$message({
                        type: 'success',
                        message: '启用认证成功!'
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消启用认证'
                    });
                });
            }
        },
        created() {

        },
        components: {
            ToolBar, HelpHint, Pagination, Edit
        }
    }
</script>
